import { authService, dbService } from "fbase";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";

export default ({ refreshUser, userObj }) => {
  const history = useHistory(); // logout   
  const [newDisplayName, setNewDisplayName] = useState(userObj.newDisplayName); //     

  const onLogOutClick = () => {
    authService.signOut(); // logout
    history.push("/");
  };

  //    
  const getMyNweets = async () => {
    const nweets = await dbService
      .collection("nweets")
      .where("creatorId", "==", userObj.uid) //  (where    .)
      .orderBy("createdAt") // 
      .get();
    console.log(nweets.docs.map((doc) => doc.data()));
  };

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewDisplayName(value);
  };

  const onSubmit = async (event) => {
    event.preventDefault();
    if (userObj.displayName !== newDisplayName) {
      await userObj.updateProfile({
        displayName: newDisplayName,
      });
      refreshUser();
      history.push("/");
    }
  };

  useEffect(() => {
    getMyNweets();
  });

  function updateUserName() {
    alert("Your username has been changed.");
  }

  return (
    <>
      <p className="changeUserName">Change Username</p>
      <form onSubmit={onSubmit}>
        <input
          className="changeName"
          onChange={onChange}
          type="text"
          value={newDisplayName}
          placeholder={userObj.displayName}
        />
        <input
          className="updateProfile"
          type="submit"
          value="Update Profile"
          onClick={updateUserName}
        />
        <span className="bar"></span>
      </form>
      <button className="logout" onClick={onLogOutClick}>
        LogOut
      </button>
    </>
  );
};
