import React from "react";
import { dbService, storageService } from "fbase";
import { useState } from "react";
import { v4 as uuidv4 } from "uuid"; //    (npm install uuid)

const NweetFactory = ({ userObj }) => {
  //   
  const [nweet, setNweet] = useState("");
  //  
  const [attachment, setAttachment] = useState("");
  const onSubmit = async (event) => {
    event.preventDefault();
    let attachmentUrl = "";
    if (attachment !== "") {
      //  
      const attachmentRef = storageService
        .ref()
        .child(`${userObj.uid}/${uuidv4()}`);
      const response = await attachmentRef.putString(attachment, "data_url");
      attachmentUrl = await response.ref.getDownloadURL();
    }
    const nweetObj = {
      text: nweet,
      createdAt: Date.now(),
      creatorId: userObj.uid,
      attachmentUrl,
      userName: userObj.displayName,
    };
    // firebase collection : nweets     
    // nweet .
    await dbService.collection("nweets").add(nweetObj);
    setNweet("");
    setAttachment("");
  };

  const onChange = (event) => {
    // event  target value 
    const {
      target: { value },
    } = event;
    setNweet(value);
  };

  const onFileChange = (event) => {
    const {
      target: { files },
    } = event;

    const theFile = files[0]; //  
    const reader = new FileReader(); //    
    //     finishedEvent 
    // finishedEvent currentTarget result 
    // setAttachment    .
    reader.onloadend = (finishedEvent) => {
      const {
        currentTarget: { result },
      } = finishedEvent;
      setAttachment(result);
    };
    reader.readAsDataURL(theFile); // readAsDataURL   .
  };

  //   
  const onClearAttachmentClick = () => setAttachment(null);

  return (
    <>
      <form onSubmit={onSubmit} className="textForm">
        <input
          className="textInput"
          value={nweet}
          onChange={onChange}
          type="text"
          placeholder="What's on your mind?"
          maxLength={120}
        />
        <input type="file" id="file" accept="image/*" onChange={onFileChange} />
        <input className="send" type="submit" value=""></input>
        <label htmlFor="file" className="photos">
          Add Photos +
        </label>
        {attachment && (
          <div>
            <img src={attachment} className="thumbnail" />
            <button onClick={onClearAttachmentClick} className="removeImage">
              Remove
            </button>
          </div>
        )}
      </form>
    </>
  );
};
export default NweetFactory;
